.nav {
    display: flex;
    align-items: end;
    justify-content: end;
    padding: 0px 0px 40px 0px;
}

.item {
    margin-right: 20px;
}

.item:last-child {
    margin-right: 0px; /* Remove margin from the last item */
}

.item a {
    text-decoration: none;
    color: black;
    font-size: 25px;
}
