*, ::before, ::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: white;
}

.container {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  /* height: 1000px; */
  background: #f1f1f1;
  margin: 100px auto 0;
  word-break: break-all;
  border: 1px solid rgba(0, 0, 0, 0.274);
  /* flex-wrap: wrap; */

}

.block-tabs {
  display: flex;
}
.tabs {

  padding: 15px;
  text-align: center;
  width: 50%;
  background: rgba(128, 128, 128, 0.075);
  cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.274);
  box-sizing: content-box;
  position: relative;
  outline: none;
  font-size: 15px;
}
.tabs:not(:last-child){
  border-right: 1px solid rgba(0, 0, 0, 0.274);
}

.active-tabs  {
  background: white;
  border-bottom: 1px solid transparent;
}

.active-tabs::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% + 2px);
  height: 5px;
  background: rgb(88, 147, 241);
}


.secondContainer{
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
 /* height: 1000px; */
  background: #f1f1f1;
  margin: 20px auto 0;
  word-break: break-all;
  border: 1px solid rgba(0, 0, 0, 0.274);
}